<template>
    <div class="surveyArchives">
        
        <!-- Header -->
        <el-row class="Header">
            <el-col :span="24">
                <div class="grid-content bg-purple-dark">
                    <div class="select-item">
                        <!-- 线路 -->
                        <p>路线：</p>
                        <el-dropdown class="select_" @command="changeLine">
                        <span class="el-dropdown-link">
                            {{save.line.routename}}<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item v-for="item in line" :key="item.id" :command="item">{{item.routename}}</el-dropdown-item>
                        </el-dropdown-menu>
                        </el-dropdown>
                        <!-- 站点 -->
                        <p style="margin-left: 20px;">站点：</p>
                        <el-dropdown class="select_"  @command="changeSite">
                        <span class="el-dropdown-link">
                            {{save.site.sitename}}<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item v-for="item in site" :key="item.id" :command="item">{{item.sitename}}</el-dropdown-item>
                        </el-dropdown-menu>
                        </el-dropdown>
                        <!-- 项目名称 -->
                        <p style="margin-left: 20px;">项目名称：</p>
                        <el-dropdown class="select_"  @command="changeProject">
                        <span class="el-dropdown-link">
                            {{save.projectMsg.pname}}<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item v-for="item in projectArr" :key="item.id" :command="item">{{item.pname}}</el-dropdown-item>
                        </el-dropdown-menu>
                        </el-dropdown>
                    </div>
                </div>
            </el-col>
        </el-row>
        <!-- Header END -->


        <div class="tableCon">
            <p style="padding-bottom: 20px;">质量分析</p>
            <template>
                <el-table
                    :data="save.tableData"
                    style="width: 100%; border: 1px solid #eee;border-bottom: none;">
                    <el-table-column label="检查项目">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.measurementitem }}</span>
                    </template>
                    </el-table-column>
                    <el-table-column label="检测方法及数量">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.inspectionmethod }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="综合检查标准或要求" width="280">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.range1 }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="管理类别">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.managementcategory }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="检查数量">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.allNumber }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="合格数量">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.hegeNumber }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="合格率%">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.hegelv }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="检查意见">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.standard1 }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="ctr(scope.$index, scope.row)">查看</el-button>
                    </template>
                    </el-table-column>
                </el-table>
            </template>
        </div>




    </div>
</template>
<script>
import api from "@/api/index.js";
export default {
    data () {
      return {
        page: '0',
        line: [],
        site: [],
        projectArr: [],
        save: {
          line: {},
          site: {},
          projectMsg: {},
          weather: {},
          tableData: []
        },
        tableData: [],
        siteID: '',
        lineID: '',
        projectID: ''
      }
    },
    methods: {
        // 改变线路
        changeLine (choose) {
            console.log(choose)
            var _this = this;
            this.save.line = choose;
            this.lineID = choose.id;
        },
        // 获取线路
        getSelectAll() {
            api.getSelectAll().then(res => {
                console.log(res.data.list)
                this.line = res.data.list;
                this.save.line = res.data.list[0];
                this.lineID = res.data.list[0].id;
            })
        },
        // 改变站点
        changeSite(choose) {
            console.log(choose);
            this.save.site = choose;
            this.siteID = choose.id;
        },
        // 改变项目
        changeProject(choose) {
            console.log(choose);
            this.save.projectMsg = choose;
            this.projectID = choose.id;
        },
        // 获取站点
        getLinesite(routeid) {
            api.getLinesite(routeid).then(res => {
                console.log('****')
                console.log(res)
                console.log(res.data.list)
                this.site = res.data.list;
                this.save.site = res.data.list[0];
                this.siteID = res.data.list[0].id;
            })
        },
        // 检测项目 列表
        getMeasurementproject(id, pageNum) {
            api.getMeasurementproject(id, pageNum, 10).then(res => {
            console.log(res.data.list)
            this.tableData = res.data.list;
            })
        },
        // 项目列表
        getMeasurementprojectSelectSelectBymid(page) {
            api.getMeasurementprojectSelectSelectBymid(this.save.site.id, page).then(res => {
                console.log(res)
                this.projectArr = res.data.list;
                this.save.projectMsg = res.data.list[0];
                this.projectID = res.data.list[0].id;
            })
        },
        // 结果
        getDetectionresultGetresultbyprojectid(id) {
            api.getDetectionresultGetresultbyprojectid(id).then(res => {
                console.log(JSON.parse(res.data.result));
                this.save.tableData = JSON.parse(res.data.result)
            })
        }

    },
    watch: {
        lineID(val) {
            console.log(val);
            this.getLinesite(val);
        },
        siteID(val) {
            console.log(val);
            this.getMeasurementprojectSelectSelectBymid(0)
        },
        projectID(val) {
            console.log(val);
            if(val) {
                this.getDetectionresultGetresultbyprojectid(val);
            } else {
                this.save.tableData = ""
            }
        }
    },
    created() {
        this.getSelectAll();
        let userInfo = JSON.parse(localStorage.getItem('USERINFO'))
        let _this = this;
        this.getSelectAll();
        console.log('USERINFO', userInfo);
        setTimeout(() => {
            this.line.forEach(item => {
            if(item.id == userInfo.line) {
                this.save.line = item;
            }
            })
            this.site.forEach(item => {
            if(item.id == userInfo.site) {
                this.save.site = item;
            }
            })
            this.save.userInfo = userInfo;
            this.lineID = userInfo.line;
            this.siteID = userInfo.site; 
        }, 200)
    },
}
</script>
<style lang="stylus" scoped>
.Header 
    color #fff
    display flex
    align-items center
    .select-item
        display flex
        align-items center
        color #fff
        .el-dropdown-link
            color #fff
.top_bt 
    display flex
    align-items center
    color #fff
    p
        margin-left 30px
.tableCon
    margin-top 16px
    padding 20px 
    border-radius 12px
    overflow hidden
    background #fff


</style>



